html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  color: var(--text-color);
  background: var(--background);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
}

.container {
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem auto;
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  justify-items: center;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .container {
    max-width: 55.625rem;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 71.875em) {
  .container {
    max-width: 69.375rem;
    grid-template-columns: repeat(4, 1fr);
    padding: 0;
  }
}
.container .subtitle {
  grid-column: 1/-1;
  justify-self: start;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: space-around;
  border-radius: 0.3125rem;
  height: 12.5rem;
  padding: 0.9375rem 0;
  width: 100%;
  max-width: 25rem;
  background: var(--card-bg);
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: solid 0.3125rem var(--facebook);
}
.card--twitter {
  border-top: solid 0.3125rem var(--twitter);
}
.card--instagram {
  position: relative;
  overflow: hidden;
}
.card--instagram:before {
  content: "";
  position: absolute;
  height: 0.3125rem;
  top: 0;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle), var(--instagram-start));
}
.card--youtube {
  border-top: solid 0.3125rem var(--youtube);
}
.card__platform {
  font-size: 0.75rem;
}
.card__icon {
  display: inline;
  width: 1.25rem;
  height: 1.25rem;
  vertical-align: middle;
  margin-right: 0.5rem;
}
.card__username {
  display: inline;
  vertical-align: middle;
  color: var(--text-color2);
}
.card__count {
  color: var(--text-color);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  color: var(--text-color2);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  align-items: center;
}
.card__change img {
  width: 0.5rem;
  height: 0.25rem;
  margin-right: 0.25rem;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}

.card2 {
  padding: 1.875rem 10%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  height: 9.375rem;
  justify-items: start;
  align-items: start;
}
.card2__description {
  color: var(--text-color2);
  font-weight: 700;
  font-size: 0.9375rem;
}
.card2__icon {
  justify-self: end;
  margin: 0;
}
.card2__count {
  align-self: end;
  font-size: 2.125rem;
}
.card2__change {
  justify-self: end;
  align-self: end;
  font-size: 1rem;
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-between;
}
@media (min-width: 40em) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}
.header__divider {
  height: 1px;
  width: 100%;
  margin: 1.25rem 0;
  background: var(--text-color2);
  opacity: 0.3;
}
@media (min-width: 40em) {
  .header__divider {
    display: none;
  }
}
.header__toggle {
  align-self: end;
}
@media (min-width: 40em) {
  .header__toggle {
    align-self: center;
  }
}

.toggle {
  border: none;
  font-size: 0.875rem;
  color: var(--text-color2);
  font-weight: 700;
  margin: 0;
  padding: 0;
}
.toggle__wrapper {
  height: 1.5rem;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  margin-left: 0.3125rem;
  margin-right: 0.3125rem;
}
.toggle label {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}
.toggle input[type=radio] {
  opacity: 0;
  height: 100%;
  width: 1.5rem;
  margin: 0 -0.125rem;
  cursor: pointer;
  position: relative;
  z-index: 1;
}
.toggle__background {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  border-radius: 0.75rem;
  z-index: 0;
}
.toggle__button {
  display: block;
  position: absolute;
  top: 10%;
  left: 5%;
  right: 50%;
  width: 40%;
  height: 80%;
  border-radius: 50%;
  background: var(--dark-bg);
  transition: all 0.1s ease-out;
  z-index: 2;
}
#light:checked ~ .toggle__button {
  left: 55%;
  right: 5%;
}